
import { useState } from 'react';
import MyButton from './MyButton.js';
import App from './App.js';
import "./styles.css";
import Downlink from './Downlink.js';
import Drawing from './Drawing.js';
//import 'bootstrap/dist/css/bootstrap.css';
import ButtonGroup from "react-bootstrap/ButtonGroup";
import Button from "react-bootstrap/Button";
import props from 'prop-types';
var React=require('react');
var ReactDOM=require('react-dom/client');
const arr1=['UPLINK','DOWN LINK','MONITORING','TOOLS'];

function Menu(){
  const [vv,setVv]=useState(0);
  function clickHandle(e){
    //console.log(e.target.value);
    if(e.target.value==='b1')
      setVv(1)
    else if(e.target.value==='b2')
      setVv(2)
      else
      setVv(3)
      //console.log(vv);
   };
      return (
        <div>
        <ButtonGroup onClick={clickHandle}>
          <Button variant="outline-primary" value='b1'>
          UPLINK
          </Button>
          <Button variant="outline-primary" value='b2'>
          DOWN LINK
          </Button>
          <Button variant="outline-primary" value='b3'>
          TOOLS
          </Button>
        </ButtonGroup>
        {vv===1?<Drawing/>:null}
        {vv===2?<Downlink/>:null}
          {console.log(vv)}
        </div>
      );
    }
const root=ReactDOM.createRoot(document.getElementById('root'));
root.render(<Menu/>);
